import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'ngx-just-alert',
  templateUrl: './just-alert.component.html',
  styleUrls: ['./just-alert.component.scss'],
})
export class JustAlertComponent implements OnInit {
  textColor: string;
  constructor() {}
  @Input() mode: any;
  @Input() close: any;
  @Input() bold: any;
  @Input() text: any;
  @Input() disabled: any;
  ngOnInit(): void {
    if (this.mode === 'success') {
      this.mode = 'green';
    } else if (this.mode === 'primary') {
      this.mode = '#0d47a1';
    } else if (this.mode === 'danger') {
      this.mode = 'crimson';
    } else if (this.mode === 'warning') {
      this.mode = '#ff9800';
    } else if (this.mode === 'info') {
      this.mode = 'cornflowerblue';
    } else {
      this.mode = 'white';
      this.textColor = 'black';
      this.bold = 'true';
    }
  }
}
